		transform: translate(-50%, -18%);
	}
	.work-main h1 {
		font-family: "Signika", sans-serif;
		font-size: 3em;
		text-align: center;
		padding: 20px 40px;
		text-transform: uppercase;
	}
	.work-main h1 a {
		color: white;
		text-decoration: none;
	}
	.work-main h1 a a:hover,
	.work-main h1 a a:focus,
	.work-main h1 a a:active {
		text-decoration: none;
	}
	.work-main h1:first-child {
		background-color: #D6B181;
	}
	.work-main h1:last-child {
		background-color: #B8D6D1;
	}
}

@media only screen and (min-width: 1000px) and (min-height: 649px) {
	body.post-type-archive-work,
	body.term-190,
	body.term-191 {
		background-position: 200px 0px;
	}
	.work-main {
		display: flex;
		flex-direction: row;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-35%, -57%);
	}
	.work-main h1 {
		font-size: 5.5em;
	}
	.work-main h1:first-child {
		margin-right: 25%;
	}
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

